.notfound {
  min-width: 100vw;
  min-height: 650px;
  display: grid;
  place-items: center;
  text-align: center;
}

.notfound-text h1 {
  font-weight: 300;
  padding: 1rem 2rem;
  margin: 0;
}

.notfound-text p {
  opacity: 0.75;
}

.notfound-back {
  color: #1856ff;
}

@media (min-width: 800px) {
  .notfound {
    min-height: 700px;
  }
}

@media (min-width: 1000px) {
  .notfound {
    min-height: 750px;
  }
}

@media (min-width: 1200px) {
  .notfound {
    min-height: 825px;
  }

  .notfound-text p {
    padding-top: 1rem;
  }
}

@media (min-width: 1500px) {
  .notfound {
    min-height: 850px;
  }
}

@media (min-width: 2000px) {
  .notfound {
    min-height: 900px;
  }
}
